/*
  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/
*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

html, body {
  height: 100%;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

#root, #__next {
  isolation: isolate;
}

html {
  font-size: 62.5%;
}

body {
  overflow-x: hidden;
  box-sizing: border-box;
  font-family: "Noto Sans JP";
  font-style: normal;
  font-weight: 100;
}

a {
  text-decoration: none;
  display: inline-block;
}

img {
  display: inline-block;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  padding: 0;
}

button {
  padding: 0;
  background-color: transparent;
  border: none;
  appearance: none;
  cursor: pointer;
}

