  height: 30px;
}

.c-svg__background--top--front {
  fill: none;
  margin-bottom: -5%;
  position: relative;
  z-index: 101;
}
.c-svg__background--top--front path {
  stroke: #BDd3DD;
}
.c-svg__background--top {
  fill: #BDd3DD;
  margin-bottom: -1%;
}
.c-svg__background--bottom {
  fill: #BDd3DD;
  transform: scaleY(-1);
  margin-top: -1%;
}
.c-svg__background--bottom--front {
  fill: none;
  margin-top: -5%;
  position: relative;
  z-index: 101;
  transform: scaleY(-1);
}
.c-svg__background--bottom--front path {
  stroke: #BDd3DD;
}

