  box-shadow: inset 2px 2px 5px rgba(78, 86, 104, 0.55);
}
.c-circle--large--white {
  background-color: #EEEBE6;
  box-shadow: inset 2px 2px 5px rgba(95, 95, 95, 0.6);
}
.c-circle--small, .c-circle--small--white {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #BDd3DD;
  box-shadow: inset 2px 2px 5px rgba(78, 86, 104, 0.55);
}
.c-circle--small--white {
  background-color: #EEEBE6;
  box-shadow: inset 2px 2px 5px rgba(95, 95, 95, 0.6);
}
.c-circle__gap {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #BDd3DD;
  box-shadow: inset 2px 2px 5px rgba(78, 86, 104, 0.55);
}

