.p-profile__wrapper {
  margin-top: 100px;
  overflow: hidden;
}
.p-profile__container {
  background-color: #BDd3DD;
  padding: 132px 20px 100px 20px;
}
.p-profile__heading {
  display: flex;
  justify-content: center;
  align-items: center;
}
.p-profile__title--font {
  z-index: 101;
}
.p-profile__title--circle--large {
  z-index: 100;
  margin-left: -26px;
}
.p-profile__title--circle--small {
  width: 68px;
  display: flex;
  justify-content: space-between;
  margin-left: 16px;
}
.p-profile__sub-heading {
  text-align: center;
}
.p-profile__group {
  display: grid;
  margin: 65px auto 0 auto;
  width: 100%;
  height: auto;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: auto;
  gap: 16px;
}
@media screen and (min-width: 768px) {
  .p-profile__group {
    width: min(95vw, 800px);
    height: 518px;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 0;
  }
}
.p-profile__self {
  margin: auto;
  background-color: #FDFDFD;
  border-radius: 16px;
  box-shadow: inset 2px 2px 6px rgba(66, 61, 60, 0.52);
  grid-column: 1/3;
  width: 100%;
  height: auto;
  padding: 10px 10px 20px 20px;
  overflow: hidden;
}
@media screen and (min-width: 768px) {
  .p-profile__self {
    grid-column: 1/3;
    width: min(64vw, 520px);
    height: 220px;
    padding: 20px 40px;
    overflow: hidden;
  }
}
.p-profile__sns {
  margin: auto;
  border-radius: 16px;
  box-shadow: inset 2px 2px 6px rgba(66, 61, 60, 0.52);
  grid-column: 2/3;
  grid-row: 2/3;
  width: 100%;
  height: 100%;
  padding: 10px 10px 20px 20px;
  overflow: hidden;
}
@media screen and (min-width: 768px) {
  .p-profile__sns {
    grid-column: 3/4;
    grid-row: 1/2;
    width: min(28vw, 240px);
    height: 220px;
    padding: 20px 40px;
    overflow: hidden;
    padding-right: 0;
  }
}
.p-profile__chara {
  background-color: brown;
  margin: auto;
  background-color: #FDFDFD;
  border-radius: 16px;
  box-shadow: inset 2px 2px 6px rgba(66, 61, 60, 0.52);
  grid-column: 1/2;
  grid-row: 2/3;
  width: 100%;
  height: 100%;
  padding: 10px 10px 20px 20px;
  overflow: hidden;
}
@media screen and (min-width: 768px) {
  .p-profile__chara {
    grid-column: 1/2;
    width: min(28vw, 240px);
    height: 272px;
    padding: 20px 40px;
    overflow: hidden;
  }
}
.p-profile__img {
  object-fit: cover;
  border-radius: 16px;
  grid-column: 1/3;
  grid-row: 3;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .p-profile__img {
    grid-column: 2/4;
    grid-row: 2/3;
    width: min(64vw, 520px);
    height: 272px;
  }
}
.p-profile__theme {
  border-bottom: 0.5px solid #b1b1b1;
