@use "../../foundation/mixin";
@use "../../foundation/variables" as val;

.c-svg {
  &__background--top--front {
    fill: none;
    margin-bottom: -5%;
    position: relative;
    z-index: 101;
    path {
      stroke: val.$mainColor;
    }
  }
  &__background--top {
    fill: val.$mainColor;
    margin-bottom: -1%;
  }
  &__background--bottom {
    fill: val.$mainColor;
    transform: scaleY(-1);
    margin-top: -1%;
  }
  &__background--bottom--front {
    fill: none;
    margin-top: -5%;
    position: relative;
    z-index: 101;
    transform: scaleY(-1);
    path {
      stroke: val.$mainColor;
    }
  }
  
}
